<template>
  <div class="container">
    <div class="box-one">
      <div class="one-title">{{ $route.query.supplierNbrDesc }}</div>
      <div class="one-arrange">
        <el-row>
          <el-col v-for="(item, index) in defineList" :key="index" :span="6">
            <div class="arrange-title">{{ item.name }}</div>
            <div class="arrange-box">
              {{ item.value ? item.value : item.formatter() }}
            </div></el-col
          >
        </el-row>
        <el-row>
          <el-col :span="24">
            <div class="arrange-title">{{ $t('rule.pushURL') }}</div>
            <div class="arrange-box">{{ detailData.actionDest }}</div></el-col
          >
        </el-row>
      </div>
    </div>

    <div v-if="tableVisible" class="box-two">
      <div class="two-title">{{ $t('rule.ruleObject') }}</div>
      <div class="table-btn">
        <el-button
          icon="el-icon-edit-outline"
          size="small"
          class="btn-blue"
          @click="clickUpload(2000)"
          >{{ $t('rule.batchImport') }}</el-button
        >
        <el-button
          icon="el-icon-delete"
          size="small"
          class="btn-red"
          @click="clickUpload(2001)"
          >{{ $t('rule.batchDelete') }}</el-button
        >
      </div>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :element-loading-text="$t('rule.loading')"
        :data="tableData"
        :border="true"
        overflow-x="scroll"
        height="483px"
      >
        <el-table-column
          prop="iccid"
          label="ICCID"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="msisdn"
          :label="$t('rule.accNumber')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <BatchUpload
      :visible.sync="showUpload"
      :query-id="queryId"
      :is-rule-batch="true"
      download-url="/order/download/"
      upload-url="/ruleCommon/upload"
      :max-file-size="10 * 1024 * 1024"
      :param="param"
      :title="
        queryId === 2000 ? $t('rule.batchImportNumber') : $t('rule.batchDeleteNumber')
      "
    >
      <template #tip>
        1、{{ $t('batchUpload.uploadTip1') }}<br />
        2、{{ $t('batchUpload.uploadTip2') }}<br />
        3、{{ $t('batchUpload.uploadTip3') }}<br />
        4、{{ $t('batchUpload.uploadTip4') }}
      </template>
    </BatchUpload>
  </div>
</template>
<script>
import BatchUpload from '@/components/BatchUpload'
import { DCP_ADDRESS_ID, CT_ADDRESS_ID } from '@/utils/constant'
import {
  unitKeyFromSubType,
  i18nKeyFromSubType,
  i18nKeyFromRuleType,
  hideObjType,
} from '../constant'
export default {
  components: { BatchUpload },
  data() {
    return {
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      showUpload: false,
      queryId: '',
      detailData: {},
      tableVisible: true,
      tableData: [],
      loading: false,
    }
  },
  computed: {
    defineList() {
      const list = [
        {
          name: this.$t('rule.ruleName'),
          formatter: () => this.detailData.ruleName,
        },
        {
          name: this.$t('rule.ruleCategory'),
          formatter: () => this.$t(i18nKeyFromRuleType(this.detailData.ruleType)),
        },
        {
          name: this.$t('rule.ruleType'),
          formatter: () => this.$t(i18nKeyFromSubType(this.detailData.ruleSubType)),
        },
        {
          name: this.$t('rule.objectType'),
          formatter: () => this.detailData.objTypeName,
        },
      ]
      if (!hideObjType) {
        const thresholds = this.detailData.ruleValue
          ? this.detailData.ruleValue.split(',').map((item, index) => ({
              name: `${this.$t('rule.threshold')}${index + 1 || ''}(${this.unitName})`, // 如果只有一个阈值，不显示序号
              value: item,
            }))
          : []

        if (thresholds.length) {
          list.push(...thresholds)
        }
      }
      return list
    },
    unitName() {
      return this.$t(unitKeyFromSubType(this.detailData.ruleSubType))
    },
    param() {
      const { createStaff, supplierNbr, supplierRuleNo, ruleId } = this.detailData
      return { userId: createStaff, supplierNbr, supplierRuleNo, ruleId }
    },
  },
  mounted() {
    this.queryRuleStrategyDetail()
    this.queryRuleSimRecordList()
  },
  methods: {
    // 查询规则策略详情
    queryRuleStrategyDetail() {
      const ruleId = this.$route.query.ruleId
      this.$api.rule.queryRuleStrategyDetail({ ruleId }).then((res) => {
        if (res.objType == 1) {
          res.objTypeName = this.$t('rule.enterprise')
        } else if (res.objType == 2) {
          res.objTypeName = this.$t('rule.package')
        } else if (res.objType == 3) {
          res.objTypeName = this.$t('rule.simCard')
        } else {
          res.objTypeName = '-'
        }
        this.detailData = res
      })
    },
    // 查询规则对象
    queryRuleSimRecordList() {
      const { supplierNbr, ruleSubType } = this.$route.query
      if (
        supplierNbr === DCP_ADDRESS_ID ||
        supplierNbr === CT_ADDRESS_ID ||
        hideObjType(Number(ruleSubType))
      ) {
        this.tableVisible = false
        return
      }
      const ruleId = this.$route.query.ruleId
      this.$api.rule.queryRuleSimRecordList({ ruleId }).then((res) => {
        if (res.list && res.list.length > 0) {
          this.tableData = res.list
          this.page.total = res.total
        } else {
          this.tableData = []
          this.page.total = 0
        }
      })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.queryRuleSimRecordList()
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.queryRuleSimRecordList()
    },
    clickUpload(queryId) {
      this.showUpload = true
      this.queryId = queryId
    },
  },
}
</script>
