export const DATA_SUB_TYPE = 101
export const SMS_SUB_TYPE = 201
export const VOICE_SUB_TYPE = 301
export const OVERFLOW_SUB_TYPE = 102 // 月用量耗尽
export const STATUS_CHANGE_SUB_TYPE = 501 // 卡状态变更
export const DEVICE_CHANGE_SUB_TYPE = 502 // 卡状态变更
export const EXPIRE_SUB_TYPE = 601 // 套餐到期

export const DATA_RULE_TYPE = 1
export const SMS_RULE_TYPE = 2
export const VOICE_RULE_TYPE = 3
export const CARD_RULE_TYPE = 5
export const PACKAGE_RULE_TYPE = 6

export const OBJ_CORP_TYPE = 1
export const OBJ_PKG_TYPE = 2
export const OBJ_SIM_TYPE = 3

// 这几种子类型没有对象类型显示
export function hideObjType(subType) {
  return [
    EXPIRE_SUB_TYPE,
    STATUS_CHANGE_SUB_TYPE,
    OVERFLOW_SUB_TYPE,
    DEVICE_CHANGE_SUB_TYPE,
  ].find((item) => item === subType) // Unknown type.
}

export function ruleTypeFromSubType(subType) {
  const subTypeToRuleTypeMap = {
    [DATA_SUB_TYPE]: DATA_RULE_TYPE,
    [SMS_SUB_TYPE]: SMS_RULE_TYPE,
    [VOICE_SUB_TYPE]: VOICE_RULE_TYPE,
    [OVERFLOW_SUB_TYPE]: DATA_RULE_TYPE,
    [STATUS_CHANGE_SUB_TYPE]: CARD_RULE_TYPE,
    [DEVICE_CHANGE_SUB_TYPE]: CARD_RULE_TYPE,
    [EXPIRE_SUB_TYPE]: PACKAGE_RULE_TYPE,
  }
  return subTypeToRuleTypeMap[subType] // Unknown type.
}

export function i18nKeyFromObjType(objType) {
  if (objType === OBJ_CORP_TYPE) {
    return 'rule.enterprise'
  } else if (objType === OBJ_PKG_TYPE) {
    return 'rule.package'
  } else if (objType === OBJ_SIM_TYPE) {
    return 'rule.simCard'
  }
  return '' // Unknown type.
}

export const subTypeToI18nKeyMap = {
  [DATA_SUB_TYPE]: 'rule.monthDataUsage', // 数据用量
  [SMS_SUB_TYPE]: 'rule.monthSmsUsage', // 短信用量
  [VOICE_SUB_TYPE]: 'rule.monthVoiceUsage', // 语音用量
  [EXPIRE_SUB_TYPE]: 'rule.packageExpire', // 月用量耗尽
  [STATUS_CHANGE_SUB_TYPE]: 'rule.simStatusChange', // 卡状态变更
  [DEVICE_CHANGE_SUB_TYPE]: 'rule.deviceChange', // 机卡变更
  [OVERFLOW_SUB_TYPE]: 'rule.monthOverflow', // 套餐到期
}
export function i18nKeyFromSubType(subType) {
  return subTypeToI18nKeyMap[subType] || ''
}

export const ruleTypeToI18nKeyMap = {
  [DATA_RULE_TYPE]: 'rule.dataRuleType',
  [SMS_RULE_TYPE]: 'rule.smsRuleType',
  [VOICE_RULE_TYPE]: 'rule.voiceRuleType',
  [CARD_RULE_TYPE]: 'rule.cardRuleType',
  [PACKAGE_RULE_TYPE]: 'rule.package',
}
export function i18nKeyFromRuleType(ruleType) {
  return ruleTypeToI18nKeyMap[ruleType] // Unknown type.
}

export function unitKeyFromSubType(subType) {
  if (subType === DATA_SUB_TYPE) {
    return 'rule.dataUnit'
  } else if (subType === SMS_SUB_TYPE) {
    return 'rule.smsUnit'
  } else if (subType === VOICE_SUB_TYPE) {
    return 'rule.voiceUnit'
  }
  return '' // Unknown type.
}

export function i18nKeyFromSendState(sendState) {
  return `rule.sendState${sendState}`
}

// 参数字段映射
export function polyFillParams(params) {
  // 如果选了套餐，字段要修正
  params.objId = params.offerId
  params.objDesc = params.offerName
}
